	module.exports = {"data":{"markdownRemark":{"html":"<p>The Program Dashboard provides information on various metrics for your program. This helps you track the progress of your program and it also gives you a detailed summary and understanding of your program status.</p>\n<p>HackerOne provides dashboards on these pages:</p>\n<ul>\n<li><a href=\"/programs/submissions-dashboard.html\">Submissions</a></li>\n<li>Statistics</li>\n<li>Response Efficiency</li>\n<li><a href=\"hacker-feedback-dashboard.html\">Hacker Feedback</a></li>\n</ul>\n<p>For each dashboard, you can customize the data you see by choosing the dates you want to see the metrics for.</p>\n<h3>Statistics</h3>\n<p>The Statistics page enables you to view overall statistics for your program. You can view who the top earning hackers are as well as the top bounties that were paid out.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-657af.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.12543554006969%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"image\"\n        title=\"\"\n        src=\"/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-2f183.png\"\n        srcset=\"/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-67829.png 125w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-5c59f.png 250w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-2f183.png 500w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-c93e8.png 750w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-4e628.png 1000w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-b3ac4.png 1500w,\n/static/program-dashboard-statistics-174934f2a4fad23d02f8f7b5f0a1f7ea-657af.png 2870w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Response Efficiency</h3>\n<p>The Response Efficiency page gives insight into how efficiently you respond to your reports. It helps you keep track of how many reports have missed your <a href=\"response-targets.html\">response target</a> or standard.</p>\n<p>You can view the metrics of the average time to:</p>\n<ul>\n<li>First response</li>\n<li>Triage</li>\n<li>Bounty</li>\n<li>Resolution</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-2e852.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.56706045865184%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"image\"\n        title=\"\"\n        src=\"/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-2f183.png\"\n        srcset=\"/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-67829.png 125w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-5c59f.png 250w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-2f183.png 500w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-c93e8.png 750w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-4e628.png 1000w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-b3ac4.png 1500w,\n/static/dashboard-response-efficiency-5c6453a7944da6248c5a86805c9fa50d-2e852.png 2878w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/dashboards.html","id":"programs/dashboards","title":"Dashboards","description":null}}},"pathContext":{}}

